import axios from '@/utils/axios';
import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import { defineComponent } from '@vue/composition-api';
import Rows from './Rows.vue';

export default defineComponent({
  props: {
    categoryId: {
      type: Number,
      required: true,
    },
  },
  render() {
    if (!this.merchantList) {
      return <div vLoading={true} style={{ height: '200px' }}></div>;
    }
    return <section style={{
      margin: '28px 0 0',
    }}>
      <div style={{
        fontWeight: 600,
        fontSize: '18px',
        marginBottom: '12px',
      }}>品牌商家推荐</div>
      <ul class="flex" style={{ margin: '-15px' }}>
          {
            this.merchantList.map(merchant => <li class="merchant-item flex-center" key={merchant.id} style={{
              width: '220px',
              height: '90px',
              padding: '10px',
              background: '#fff',
              margin: '15px',
              borderRadius: '8px',
              boxShadow: '0px 0px 9px 0px rgba(136, 149, 172, 0.2)',
            }}>
              <img onclick={() => this.handleClickMerchant(merchant)}
              style={{ objectFit: 'contain', maxWidth: '100%', maxHeight: '100%' }}
              src={merchant.image} alt={merchant.image}/>
            </li>)
          }
      </ul>
    </section>;
  },
  inject: ['isQt'],
  setup(props, { root }) {
    type MerchantInfo = {
      id: string
      image: string
      name: string
    };
    const merchantList = useLoader(async ({ categoryId }) => {
      const res = (await axios.get<MerchantInfo[]>(`/mall/recommend/merchant?categoryId=${categoryId}`)).data;
      return res;
    }, props);
    function handleClickMerchant(m: MerchantInfo) {
      root.$router.push({
        path: `${this.isQt() ? '/qt' : ''}/supplier-page/preview/homepage`,
        query: {
          companyId: m.id,
        },
      });
    }
    return {
      merchantList,
      handleClickMerchant,
    };
  },
});
